.popup__error {
  opacity: 0;
  margin: 30px auto 0 auto;
  width: 358px;
}

.popup__error:last-of-type {
  margin: 0 auto 48px auto;
}

@media screen and (max-width: 638px) {
  
  .popup__error {
    width: 238px;
  }
  .popup__error:last-of-type {
    margin: 0 auto 45px auto;
  }

}
